<template>
  <div :class="['select', ...customClasses, {'open': isActive}, {'invalid': isInvalid}]" v-click-outside="onClickOutside">
    <div class="select__backdrop" @click="isActive = false"></div>
    <div :class="['select__input']">
      <slot name="icon"></slot>
      <span :class="['select__label', {'open': isActive || null !== selectedItem.label && selectedItem.label.length > 0 }]">{{ label }}</span>
      <input
        :class="['select', 'select__value', {'filled':  null !== selectedItem.label && selectedItem.label.length > 0}]"
        v-model="selectedItem.label"
        @focus="isActive = true"
        :placeholder="placeholder"
        :readonly="isReadOnly"
        @input="setText"
        @blur="$emit('blur')"
      >
      <div class="select__arrow" @click.prevent="isActive = !isActive">
        <slot name="arrow"><span :class="['icon', {'icon-up': isActive, 'icon-down': !isActive}]"></span></slot>
      </div>
    </div>
    <div class="select__dropdown" v-if="items.length > 0">
      <ul class="select__list">
        <li
          v-for="(item, key) in items"
          :key="item.value"
          :class="['select__item', {'selected': item.value === selectedItem.value}]"
          :data-value="item.value"
          @click.prevent="selectItem(item)"
        >{{ item.label }}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "Select",
  props: {
    defaultItem: [Object, String, Number],
    items: {
      type: [Array, Object],
      default() {
        return []
      }
    },
    placeholder: {
      type: String,
      default: '',
    },
    label: {
      type: String,
      default: '',
    },
    isInvalid: {
      type: Boolean,
      default: false,
    },
    isReadOnly: {
      type: Boolean,
      default: true,
    },
    customClasses: {
      type: [Array, String],
      default: '',
    },
  },
  data() {
    return {
      selectedItem: {
        value: null,
        label: null,
      },
      isActive: false,
    };
  },
  methods: {
    setText(event) {
      if(!this.isReadOnly) {
        this.$emit('inputText', event.target.value);
      }
    },
    selectItem(item) {
      this.isActive = false;
      this.selectedItem = Object.assign({}, item);

      this.$emit('selected', this.selectedItem);
    },
    onClickOutside() {
      this.isActive = false;
    },
    formatItems() {
      for(let i = 0; i < this.items.length; i++) {
        if(this.items[i].labelCode) {
          this.items[i].label = this.$ml.get(this.items[i].labelCode);
        }
      }
    },
  },
  mounted() {
    this.formatItems();

    let bus = this.$ml.bus;
    let _this = this;

    bus.$on('vueml-language-changed', function (newLanguage) {
      _this.formatItems();
    });

    if (this.defaultItem) {
      if(typeof this.defaultItem === 'object') {
        let item = this.items.find(item => item.value === this.defaultItem);
        if (item) {
          this.selectedItem = item;
        }
      } else if(typeof this.defaultItem === 'string') {
        this.selectedItem.label = this.defaultItem;
        this.selectedItem.value = this.defaultItem;
      }
    }
  }
}
</script>

<style scoped lang="scss">
  @import "~@/assets/scss/components/select.scss";
</style>