<template>
  <div :class="['edit-memorial', {'editing': isEditing}]">

    <Header
        :user="user"
        @changeLanguage="changeLanguage"
        @changePlatform="changePlatform"
        @setEditingBlock="closeSidebar"
        :previewRoute="'memorials.life'"
    />

    <div class="edit-memorial__content">
      <StorySidebar
          v-if="isEditing"
          :isEditing="isEditing"
          :title="$ml.get('editMemorial.sidebar.periods.title')"
          @close="closeSidebar"
          @setEditingBlock="closeSidebar"
      >
        <component
            slot="sidebar-content"
            :is="`${editBlock}-sidebar`"
            :memorial="memorial"
            :periodModel="period"
            :key="period.id"
            @updateStory="updateStory"
            @updatePeriod="updatePeriod"
            @setEditingBlock="closeSidebar"
            @refreshPeriod="refreshPeriod"
        />
      </StorySidebar>

      <div class="edit-memorial__wrapper">
        <div :class="['edit-memorial__container', platform]" id="edit-memorial" :dir="'he' === lang ? 'rtl' : 'ltr'">

          <div class=" edit-memorial__block-disabled">
            <div class="edit-memorial__overlay"></div>

             <div v-if="memorial" class="fixed-header life-main-header">

            <div class="fixed-header__container">
              <img class="header-screen-hamburger" src="~@/assets/img/icon/hamburger.svg" alt="">
              <div class="fixed-header__box">
                <div class="fixed-header__link-wrapper">
                  <span class="fixed-header__link fixed-header__link--bigger">{{ memorial.name }}</span>
                  <router-link :to="{ name: 'home' }" class="fixed-header__link-sub">
                    <svg xmlns="http://www.w3.org/2000/svg" width="9.792" height="15.857"
                         viewBox="0 0 9.792 15.857">
                      <path id="Path_1413" data-name="Path 1413"
                            d="M9.792,13.994,3.74,7.929,9.792,1.863,7.929,0,0,7.929l7.929,7.929Z"
                            fill="currentColor"/>
                    </svg>
                    {{ $ml.get('memorial.header.toHome') }}
                  </router-link>
                </div>
                <router-link :to="{ name: 'memorials.life', params: { slug: this.memorial.slug } }"
                             class="fixed-header__link fixed-header__life-story fixed-header__link--margin active"
                >{{ $ml.get('memorial.header.life') }}
                </router-link>
                <router-link :to="{ name: 'memorials.relatives', params: { slug: this.memorial.slug } }"
                             class="fixed-header__link"
                >{{ $ml.get('memorial.header.relatives') }}
                </router-link>
              </div>
              <div class="fixed-header__logo">
                <router-link :to="{ name: 'home' }">
                  <img class="card__logo" src="~@/assets/img/logo.svg" alt="Logo">
                </router-link>
              </div>

              <div class="fixed-header__logo--mod">
                <router-link :to="{ name: 'home' }">
                  <img class="card__logo" src="~@/assets/img/icon/small-logo.svg" alt="Logo">
                </router-link>
              </div>
            </div>
          </div>

             <section class="life-main" v-if="memorial">

            <div class="life-main__container">
              <div class="life-main__arrow">
                <a href="#periods" v-smooth-scroll>
                  <svg xmlns="http://www.w3.org/2000/svg" width="98" height="44.141" viewBox="0 0 98 44.141">
                    <g id="Group_12291" data-name="Group 12291" transform="translate(-29 -1010)">
                      <path id="Path_6541" data-name="Path 6541"
                            d="M24.515,0C38.054,0,49.03,9.881,49.03,22.07v22.07H0V22.07C0,9.881,10.976,0,24.515,0Z"
                            transform="translate(53.515 1010)" fill="currentColor"/>
                      <path id="Intersection_2" data-name="Intersection 2"
                            d="M25,25v0ZM0,25V.02A24.85,24.85,0,0,0,1.965,9.732a24.925,24.925,0,0,0,5.358,7.946,24.919,24.919,0,0,0,7.946,5.358A24.835,24.835,0,0,0,24.729,25H0Z"
                            transform="translate(102 1029.141)" fill="currentColor"/>
                      <path id="Intersection_1" data-name="Intersection 1"
                            d="M.181,25a24.845,24.845,0,0,0,9.551-1.964,24.911,24.911,0,0,0,7.946-5.358,24.9,24.9,0,0,0,5.358-7.946A24.832,24.832,0,0,0,25,.179V25ZM0,25V0H0V25Z"
                            transform="translate(29 1029.14)" fill="currentColor"/>
                      <g id="baseline-arrow_back-24px" transform="translate(63.004 1046.836) rotate(-90)"
                         opacity="0.997">
                        <g id="baseline-arrow_back-24px-2" data-name="baseline-arrow_back-24px"
                           opacity="0.996">
                          <path id="Path_1968" data-name="Path 1968" d="M0,0H29.769V29.769H0Z"
                                fill="none"/>
                          <path id="Path_1969" data-name="Path 1969"
                                d="M20.846,11.37H8.033l5.886-5.886L12.423,4,4,12.423l8.423,8.423,1.485-1.485L8.033,13.476H20.846Z"
                                transform="translate(2.462 2.462)" fill="#083d58"/>
                        </g>
                      </g>
                    </g>
                  </svg>
                </a>
              </div>

              <!-- photos-block -->
              <div class="life-main__photo-block photos-block">
                <div class="photos-block__img photos-block__img--1" :class="{'photos-block__without-image': !getOnePeriodPhoto(0)}">
                  <div class="life-period__mask"></div>
                  <img class="photos-block__img-1" :src="getOnePeriodPhoto(0)" alt="">
                  <img class="photos-block__img-2" src="~@/assets/img/icon/life-baseline.svg" alt="">
                </div>

                <div class="photos-block__img photos-block__img--2" :class="{'photos-block__without-image': !getMemorialPhoto()}">
                  <div class="life-period__mask"></div>
                  <img class="photos-block__img-1" :src="getMemorialPhoto()" alt="">
                  <img class="photos-block__img-2" src="~@/assets/img/icon/life-face.svg" alt="">
                </div>

                <div class="photos-block__img photos-block__img--3" :class="{'photos-block__without-image': !getOnePeriodPhoto(1)}">
                  <div class="life-period__mask"></div>
                  <img class="photos-block__img-1" :src="getOnePeriodPhoto(1)" alt="">
                  <img class="photos-block__img-2" src="~@/assets/img/icon/life-picture.svg" alt="">
                </div>

                <div class="photos-block__img photos-block__img--4" :class="{'photos-block__without-image': !getOnePeriodPhoto(2)}">
                  <div class="life-period__mask"></div>
                  <img class="photos-block__img-1" :src="getOnePeriodPhoto(2)" alt="">
                  <img class="photos-block__img-2" src="~@/assets/img/icon/life-videocam.svg" alt="">
                </div>
              </div>
              <div class="life-main__title-wrapper" v-if="periods !== undefined">
                <h1 class="life-main__title first-header">{{ $ml.get('life.periods.title') }}</h1>
                <div class="life-main__box">
                  <div class="life-main__scale">{{ $ml.get('life.periods.desc') }}</div>
                  <div class="life-main__decor"></div>
                  <div class="life-main__years">{{ getTimelineRange }}</div>
                </div>

                <ul class="life-main__life-points" v-if="periodsWithYears && periodsWithYears.length">
                  <li class="life-main__point"
                      v-for="period in periodsWithYears"
                      :key="period.id"
                      :href="`#${period.id}`"
                      v-smooth-scroll
                  >
                    <div class="life-main__descr">{{ period.name }}</div>
                    <span class="life-main__date">{{ period.since | getYear }} - {{ period.till | getYear }}</span>
                  </li>
                </ul>

                <div class="life-main__for-memory" v-if="periodsWithoutYears && periodsWithoutYears.length">
                  <div class="life-main__text"
                       v-for="period in periodsWithoutYears"
                       :key="period.id"
                       :href="`#${period.id}`"
                       v-smooth-scroll
                  >{{ period.name }}
                  </div>
                </div>
              </div>
            </div>

            <router-link v-if="$store.getters['auth/isLoggedIn'](['customer'])"
                         :tag="'button'"
                         :to="{ name: 'memorials.story.create', params: { slug: memorial.id } }" type="button"
                         class="default-btn-plus float-button">
              <img src="~@/assets/img/icon/blue-plus.svg" alt="">{{ $ml.get('memorial.floatButton') }}
            </router-link>
            <router-link v-if="!$store.getters['auth/isLoggedIn'](['customer'])"
                         :tag="'button'"
                         :to="{ name: 'memorials.story.auth', params: { slug: memorial.slug } }" type="button"
                         class="default-btn-plus float-button">
              <img src="~@/assets/img/icon/blue-plus.svg" alt="">{{ $ml.get('memorial.floatButton') }}
            </router-link>
          </section>

          </div>


          <section class="life-period" id="periods" v-if="periods && periods.length">
            <!-- life-main__container life-period__container -->
            <div class="life-main__container life-period__container"
                 v-for="(period, pk) in periods" :key="period.id" :id="period.id">
              <!-- life-period__order -->
              <div class="life-period__order life-period__order--padding">
                <div class="life-period__title second-header"><span>{{ period.name }}</span></div>
                <div class="life-period__date" v-if="period.since">{{ period.since | getYear }} - {{ period.till | getYear }}</div>
                <router-link v-if="$store.getters['auth/isLoggedIn'](['customer'])"
                             :tag="'button'"
                             :to="{ name: 'memorials.story.create', params: { slug: memorial.id, period: period.id } }" type="button"
                             class="life-period__btn">
                  <img src="~@/assets/img/icon/button-white-plus.svg" alt="">{{ $ml.get(`editMemorial.life.${lang}.createStory`) }}
                </router-link>
                <router-link v-if="!$store.getters['auth/isLoggedIn'](['customer'])"
                             :tag="'button'"
                             :to="{ name: 'memorials.story.share', params: { slug: memorial.slug, period: period.id } }" type="button"
                             class="life-period__btn">
                  <img src="~@/assets/img/icon/button-white-plus.svg" alt="">{{ $ml.get(`editMemorial.life.${lang}.createStory`) }}
                </router-link>
              </div>

              <!-- life-period__section -->
              <div class="life-period__section life-period__section--padding">

                <div v-for="(story, key) in period.all" :key="story.id">
                  <div class="life-period__user-wrapper">
                    <!-- life-period__user -->
                    <div class="life-period__user">
                      <div class="life-period__user-img">
                        <img :src="story.owner.user.profilePhoto ? story.owner.user.profilePhoto.url : require('@/assets/img/icon/form-human.svg')"
                             :alt="story.owner.user.name">
                      </div>
                      <div class="life-period__box">
                        <div class="life-period__user-name">{{ story.owner.user.name }}</div>
                        <div class="life-period__user-relation">{{ story.owner.relationToMemorialPerson }}
                        </div>
                      </div>
                    </div>

                    <div class="life-period__text">
                      {{ story.description }}
                    </div>
                  </div>

                  <div v-for="(item, key) in story.content">
                    <ImageStory v-if="item.contentType == 'image'" :addClass="'life-period__first-photo'"
                                :item="item" :key="'image-'+item.id"/>
                    <ImageStory v-else-if="item.type == 'link'" :addClass="'life-period__first-photo'"
                                :item="item" :key="'image-'+item.id"/>
                    <AudioStory v-else-if="item.contentType == 'audio'" :addClass="['post__qoute-audio']" :item="item" :key="'audio-'+item.id"/>
                    <VideoStory v-else-if="item.contentType == 'video'" :item="item" :key="'video-'+item.id"/>
                  </div>
                  <div v-if="key != (period.all.length - 1)" class="life-period__decor"></div>
                </div>
              </div>
            </div>
          </section>

        </div>
      </div>
    </div>

    <HelpCenter
      :withStoryBtn="true"
      :memorialSlug="memorial.id"
    />
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import Swiper, { Navigation } from 'swiper';
Swiper.use([Navigation]);
import { EventBus } from '@/utils/EventBus.js';

import HelpCenter from '@/components/HelpCenter.vue';
import Header from './partials/Header';
import LifePeriodSidebar from '@/components/stories/sidebars/LifePeriod';
import StorySidebar from '@/components/StorySidebar';
import Button from '@/components/Button';
import ImageStory from "@/components/storiesFull/ImageStory";
import AudioStory from "@/components/storiesFull/AudioStory";
import VideoStory from "@/components/storiesFull/VideoStory";

export default {
  name: "EditLife",
  components: {
    Header,
    StorySidebar,
    LifePeriodSidebar,
    Button,
    ImageStory,
    AudioStory,
    VideoStory,
    HelpCenter
  },
  data() {
    return {
      isEditing: false,
      editBlock: '',
      stories: [],
      swiperOptions: {
        direction: 'horizontal',
        loop: false,
        slidesPerView: 3,
        spaceBetween: 20,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
      },
      lang: 'en',
      platform: 'desktop',
      storiesSlider: null,
    };
  },
  computed: {
    ...mapGetters({
      memorial: 'dashboard/currentMemorial',
      user: 'profile/getUser',
      isLoggedIn: 'auth/isLoggedIn',
      token: 'auth/token',
      periods: 'period/getPeriodsFiltered',
      period: 'period/getPeriodEdit',
      periodsWithYears: 'period/getPeriodsWithYears',
      years: 'period/getYears',
      periodsWithoutYears: 'period/getPeriodsWithoutYears',
      getTimelineRange: 'period/getTimelineRange',
    }),
  },
  methods: {
    updateMemorial() {

    },
    closeSidebar() {
      this.$router.push({name: 'memorials.edit', params: { id: this.memorial.id }, hash: '#life-story'});
    },
    changeLanguage(lang) {
      this.lang = lang;
    },
    changePlatform(platform) {
      this.platform = platform;
    },
    openEdit(block = 'default') {
      this.isEditing = true;
      this.editBlock = block;

      if(typeof this.$refs[this.editBlock] !== 'undefined') {
        let _this = this;
        setTimeout(() => {
          _this.$refs[this.editBlock].scrollIntoView({block: "start", behavior: "smooth"});
        }, 1);
      }
    },
    setEditing() {
      this.edit('common');
    },
    edit(block) {
      if(this.isEditing) {
        this.isEditing = false;
        this.editBlock = '';
      } else {
        this.isEditing = true;
        this.editBlock = block;

        if(typeof this.$refs[this.editBlock] !== 'undefined') {
          let _this = this;
          setTimeout(() => {
            _this.$refs[this.editBlock].scrollIntoView({block: "start", behavior: "smooth"});
          }, 1);
        }
      }
    },
    isMemorialFieldExist(field) {
      return typeof this.memorial[field] !== 'undefined' && null !== this.memorial[field];
    },
    getMemorialPhoto() {
      return this.memorial.profilePhoto ? this.memorial.profilePhoto.url : null;
    },
    getOnePeriodPhoto(index) {
      if (this.periods && this.periods.length && this.periods[index] !== undefined) {
        let period = this.periods[index];

        if(period.thumbnail) {
          return period.thumbnail.url;
        } else {
          if(period.defaultImageName) {
            return require(`@/assets/img/icon/periods/${period.defaultImageName}/1x.png`);
          }
        }
      }

      return null;
    },
    async updatePeriod(period) {
      let selectedPeriod = this.$route.params.period;
      let {id, all, thumbnail, isGeneral, ...data} = period

      const payload = {
        id: period.id,
        data
      };

      const response = await this.$store.dispatch('period/update', payload);

      if(response.status == true) {
        await this.$store.dispatch('period/getPeriodEditStories', {
          id: selectedPeriod,
        });

        await this.$store.commit('period/setPeriodData', response.data);
      }
    },
    async updateStory(story) {
      let selectedPeriod = this.$route.params.period;

      const payload = {
        id: story.id,
        data: {
          editVersion: story.editVersion,
          visible: !story.visible,
        },
      };

      const response = await this.$store.dispatch('story/setVisible', payload);

      if(typeof response.id !== "undefined") {
        await this.$store.commit('period/setPeriodStoryVisible', response);

        await this.$store.dispatch('period/getPeriods', {
          id: this.memorial.id,
          storyType: 'all',
          withGeneral: true,
          selected: selectedPeriod
        });

        this.stories = this.periods.length ? this.periods[0].all : [];
      }
    },
    async refreshPeriod() {
      let selectedPeriod = this.$route.params.period;

      await this.$store.dispatch('period/getPeriods', {
        id: this.memorial.id,
        storyType: 'all',
        withGeneral: true,
        selected: selectedPeriod
      });

      await this.$store.dispatch('period/getPeriodEditStories', {
        id: selectedPeriod,
      });

      this.stories = this.periods.length ? this.periods[0].all : [];
    }
  },
  watch: {
    editBlock(block) {
      switch (block) {
        case 'life-story':
          this.sidebarTitle = this.$ml.get('editMemorial.sidebar.periods.title');
          break;
        default:
            this.sidebarTitle = '';
            break;
      }
    }
  },
  async mounted() {

    document.querySelector('.edit-memorial__wrapper').addEventListener("click", function(e) {
      // console.log(e);
      e.stopPropagation();
      e.preventDefault();
    },true);

    let selectedPeriod = this.$route.params.period;

    if (!this.token) {
      await this.$store.dispatch('auth/getToken');
    }

    if (!this.memorial) {
      await this.$router.push({name: 'home'});
    }
    else {
      await this.$store.dispatch('period/getPeriods', {
        id: this.memorial.id,
        storyType: 'all',
        withGeneral: false,
        selected: selectedPeriod
      });

      await this.$store.dispatch('period/getPeriodEditStories', {
        id: selectedPeriod,
      });

      this.stories = this.periods.length ? this.periods[0].all : [];

      this.openEdit('life-period')
    }

    if(selectedPeriod) {
      const myEl = document.getElementById(selectedPeriod);

      if(myEl) {
        setTimeout(() => {
          myEl.scrollIntoView({block: "start", behavior: "smooth"});
        }, 1);
      }
    }
  },
  async created() {

    await this.$store.commit('period/setPeriods', []);
    this.$store.commit('memorial/setDefaultData');

    this.lang = this.$ml.current;

    await this.$store.dispatch('dashboard/setCurrentMemorial', this.memorial.id);
  },
}
</script>

<style lang="scss">
  @import "~@/assets/scss/pages/life-story.scss";
  @import "~@/assets/scss/pages/memorial-edit.scss";
  @import "~@/assets/scss/components/create-period.scss";
</style>

<style lang="scss" scoped>
  .life-main {
    width: 100%;
  }

  .edit-memorial__block-disabled {
    height: 100vh;
    z-index: 10;
  }

  .life-period {
    width: 100%;
  }

  .life-period__order {
    background-color: #eaeaea;
  }

  .life-main__arrow {
    @media(max-width: 1199.98px) {
      a {
        color: #eaeaea;
      }
    }
  }
  .plyr--audio .plyr__controls {

    @media(max-width: 1199.98px) {
      background-color: #eaeaea;
    }
  }

  .life-main__photo-block {
    position: absolute;
    padding-top: 45.5%;
    width: 40%;
    bottom: 6.5%;
    left: 11%;
    z-index: 12;
  }

  .life-main .photos-block__img--2 {
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    padding-top: unset;
  }
</style>