<template>
  <div
      :class="['custom-input', {'invalid': invalid}, ...classList]"
  >
    <span class="label" v-if="label">{{ label }}</span>

    <input
        :value="inputVal"
        ref="input"
        @input="$emit('update:inputVal', $event.target.value)"
        @blur="emitBlur"
        @focus="isFocus = true"
        :readonly="readOnly"
    />

    <div v-if="invalid" class="custom-input__error">
      <span class="icon icon__error error-icon" v-if="!isFocus"></span>
      <span>{{ errorLabel }}</span>
    </div>

    <span
        class="icon icon__clear clear"
        @click.prevent.stop="clearInput"
        v-if="inputVal.length > 0"
    ></span>

    <span class="icon icon__error error" v-if="invalid && inputVal.length === 0 && isFocus"></span>
  </div>
</template>

<script>
export default {
  name: "Input",
  props: {
    classList: {
      type: Array,
      default() {
        return [];
      },
    },
    inputVal: {
      type: String,
      default: '',
    },
    label: {
      type: String,
      default: '',
    },
    invalid: {
      type: Boolean,
      default: false,
    },
    errorLabel: {
      type: String,
      default: '',
    },
    readOnly: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
      showClear: false,
      isFocus: false,
    };
  },
  methods: {
    clearInput() {
      this.$emit('update:inputVal', '')
      this.$refs['input'].focus();
    },
    emitBlur() {
      this.isFocus = false;
      this.$emit('blur');
    }
  },
}
</script>

<style scoped lang="scss">
  @import "~@/assets/scss/utils/vars.scss";
  @import "~@/assets/scss/components/input.scss";
</style>