<template>
  <!-- the third sidebar -->
  <div class="memorial-sidebar__history " style="display: block;">
    <Story
      v-if="isShowStory"
      :storyId="showStoryId"
      :editVersion="showStoryEditVersion"
      :isVisible="showStoryVisible"
      @close="closeModal"
      @approve="approveStory"
    />

    <div class="memorial-sidebar__item">
      <div class="memorial-sidebar__item-wrapper">
        <div class="memorial-sidebar__period">
          <div class="memorial-sidebar__period-title">{{ $ml.get(`editMemorial.sidebar.life.title`) }}</div>
        </div>
        <form class="memorial-sidebar__form create-period">
          <div class="memorial-sidebar__group period-group z-index-5" :class="{'invalid': bindClassInvalid('name')}">
            <div class="create-period__image" v-if="periodImageUrl">
              <img ref="periodImg" :src="periodImageUrl" alt="">
            </div>

            <Select
              :placeholder="$ml.get('periods.create.form.periods.name')"
              :label="$ml.get('periods.create.form.periods.name')"
              :items="periodItems"
              :defaultItem="period"
              :isReadOnly="false"
              :key="period.id"
              :isInvalid="!isPeriodValid"
              @selected="selectPeriodName"
              @inputText="setPeriodName"
              @blur="updatePeriod('name')"
            >
              <div class="create-period__select-icon" slot="icon">
                <img src="~@/assets/img/icon/general-period.svg" alt="">
              </div>
            </Select>
          </div>

          <div class="create-period__poster">
            <input v-if="!periodImageUrl" type="file" id="periodImage" class="input-hide" accept="image/*" @change="uploadImage">
            <label v-if="!periodImageUrl" for="periodImage" class="poster-add">
              <span class="plus-squared"><span></span></span>
              <span class="text">{{ $ml.get('periods.create.form.periods.image.add') }}</span>
            </label>

            <span class="poster-remove" v-if="periodImageUrl" @click="removeImage">{{ $ml.get('periods.create.form.periods.image.remove') }}</span>
          </div>

          <div class="">
            <div class="create-period__half-content">
              <div
                :class="['create-period__range', {'invalid': !isRangeValid || bindClassInvalid('till') || bindClassInvalid('since')}]">
                <date-range-picker
                  ref="picker"
                  :autoApply="true"
                  v-model="range"
                  :ranges="false"
                  :show-dropdowns="true"
                  :opens="$ml.current === 'he' ? 'left' : 'right'"
                  :locale-data="$ml.current === 'he' ? localeOptions.he : localeOptions.en"
                  :minDate="minDate"
                  :maxDate="maxDate"
                  @toggle="rangeToggle"
                  @update="updatePeriod('range')"
                >
                  <template v-slot:input="range">
                    <div v-if="range.startDate && range.endDate">
                      <span class="label">{{ $ml.get('periods.create.form.periods.range') }}</span>
                      <span class="years">{{ range.startDate | getYear }} - {{ range.endDate | getYear }}</span>
                    </div>

                    <div class="" v-else>
                      <span class="label__placeholder">{{ $ml.get('periods.create.form.periods.range') }}</span>
                    </div>

                  </template>
                </date-range-picker>


                <div class="range__wrapper" @click.prevent="$refs.picker.stop.togglePicker(!isShowRange)">
                  <span class="icon icon__calendar icon-range"
                        @click.prevent.stop="$refs.picker.togglePicker(!isShowRange)"></span>
                </div>
              </div>

              <div class="create-period__location">
                <Input
                  :placeholder="$ml.get('periods.create.form.periods.location')"
                  :label="$ml.get('periods.create.form.periods.location')"
                  :class="['location__input']"
                  :inputVal.sync="location"
                  :invalid="!isLocationValid"
                  @blur="updatePeriod('location')"
                />
              </div>
            </div>

          </div>
        </form>
      </div>
    </div>

    <div class="memorial-sidebar__item">
      <div class="memorial-sidebar__item-wrapper">
        <div class="memorial-sidebar__period-title memorial-sidebar__period-title--mod">
          {{ $ml.get('editMemorial.sidebar.life.stories.title') }}
          <img src="~@/assets/img/icon/ask-yelow.svg" alt="" @click="isTooltipShow = !isTooltipShow">
        </div>

        <div class="memorial-sidebar__histories">
          {{ $ml.get('editMemorial.sidebar.life.stories.desc') }}
        </div>

        <button class="memorial-sidebar__history-btn"
                @click="addStory"
        >
          <img src="~@/assets/img/icon/blue-plus.svg" alt="">
          <span>
            {{ $ml.get('editMemorial.sidebar.life.stories.create') }}
          </span>
        </button>

        <div class="memorial-sidebar__card"
             v-for="story in periodModel.all" :key="story.id">
          <div class="memorial-sidebar__card-top">
            <div class="memorial-sidebar__user">
              <div class="memorial-sidebar__user-img">
                <img
                  :src="story.owner.user.profilePhoto ? story.owner.user.profilePhoto.url : require('@/assets/img/icon/form-human.svg')"
                  :alt="story.owner.user.name">
              </div>
              <div class="memorial-sidebar__user-box">
                <div class="memorial-sidebar__user-name">
                  {{ story.owner.user.name }}
                </div>

                <div class="memorial-sidebar__society">
                  {{ story.owner.relationToMemorialPerson }}
                </div>
              </div>
            </div>
            <button class="memorial-sidebar__card-btn"
                    @click.prevent="showStory(story)"
            >
              {{ $ml.get('editMemorial.sidebar.life.stories.show') }}
            </button>
          </div>

          <div class="memorial-sidebar__card-bottom" :data-id="story.id">
            <div
              class="memorial-sidebar__switcher-wrapper memorial-sidebar__switcher-wrapper--mod memorial-sidebar__switcher-wrapper--indent-2">
              <img src="~@/assets/img/icon/post-icon.svg" alt="">
              <span v-html="story.period.name"></span>
            </div>
            <div
              class="memorial-sidebar__switcher-wrapper memorial-sidebar__switcher-wrapper--mod memorial-sidebar__switcher-wrapper--indent"
            >
              <!-- при клике добавлять и убирать класс - switch -->
              <div class="memorial-sidebar__switcher-box" :class="{'switch': !story.visible}"
                   @click.prevent="updateStory(story)">
                <div class="memorial-sidebar__switcher-point"></div>
              </div>
              <span v-if="story.visible">
                  {{ $ml.get('editMemorial.sidebar.life.stories.visible') }}
              </span>
              <span v-else>
                {{ $ml.get('editMemorial.sidebar.life.stories.notVisible') }}
              </span>
            </div>
          </div>
        </div>

      </div>
    </div>

    <Tooltip
        v-if="isTooltipShow"
        :label="$ml.get('editMemorial.sidebar.life.stories.help.label')"
        :withTalkLink="false"
        @close="isTooltipShow = false"
    >
      <div slot="content" class="tooltip__description">
        <span>
           {{ $ml.get('editMemorial.sidebar.life.stories.help.text') }}
        </span>
      </div>
    </Tooltip>
  </div><!-- the third sidebar -->
</template>

<script>
  import Vue from 'vue';
  import {mapGetters} from "vuex";
  import Button from '@/components/Button';
  import Select from '@/components/Select';
  import Input from '@/components/Input';
  import {en, he} from "vuejs-datepicker/dist/locale";
  import DateRangePicker from 'vue2-daterange-picker'
  import Story from "@/views/dashboard/Story";
  import Tooltip from "@/components/Tooltip";

  import 'vue2-daterange-picker/dist/vue2-daterange-picker.css'

  export default {
    name: "LifePeriod",
    components: {
      Input,
      Select,
      Button,
      DateRangePicker,
      Story,
      Tooltip,
    },
    props: {
      memorial: {
        type: Object,
        default() {
          return {};
        }
      },
      periodModel: {
        type: Object,
        default() {
          return {};
        }
      },
    },
    data() {
      return {
        isTooltipShow: false,
        period: '',
        periodImage: null,
        periodImageUrl: null,
        periodImageFile: null,
        periodUploadedFile: null,
        location: '',
        range: {
          startDate: '',
          endDate: '',
        },
        isShowRange: false,
        locale: 'he',
        isShow: true,
        isUpdatePeriod: false,
        isPeriodValid: true,
        isRangeValid: true,
        isLocationValid: true,
        periodItems: [],

        validationRules: {
          period: {
            min: 1,
            max: 244,
          },
        },

        localeOptions: {
          he: {
            direction: 'rtl',
            format: 'yyyy',
            daysOfWeek: ['א', 'ב', 'ג', 'ד', 'ה', 'ו', 'ש'],
            monthNames: ['ינו', 'פבר', 'מרץ', 'אפר', 'מאי', 'יונ', 'יול', 'אוג', 'ספט', 'אוק', 'נוב', 'דצמ'],
            firstDay: 0
          },
          en: {
            direction: 'ltr',
            format: 'yyyy',
            separator: ' - ',
            daysOfWeek: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
            monthNames: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            firstDay: 0
          },
        },

        isShowStory: false,
        showStoryId: null,
        showStoryEditVersion: null,
        showStoryVisible: false,
      }
    },
    computed: {
      ...mapGetters({
        errors: 'period/getErrors',
      }),
      minDate() {
        let dY = new Date();
        dY.setFullYear(dY.getFullYear() - 120);
        return dY;
      },
      maxDate() {
        return new Date();
      },
    },
    mounted() {
    },
    methods: {
      bindClassInvalid(key) {
        return this.errors[key];
      },
      showErrorText(key) {
        return this.errors[key];
      },
      addStory() {
        this.$router.push({
          name: 'memorials.story.create',
          params: {slug: this.memorial.id, period: this.periodModel.id}
        });
      },
      showStory(story) {
        this.isShowStory = true;
        this.showStoryId = story.id;
        this.showStoryEditVersion = story.editVersion;
        this.showStoryVisible = story.visible;
      },
      closeModal() {
        this.isShowStory = false;
        this.showStoryId = null;
        this.showStoryEditVersion = null;
        this.showStoryVisible = false;
        this.$store.dispatch('story/saveStory', {});
        this.$emit('refreshPeriod');
      },
      async approveStory() {
        this.closeModal();
      },

      validate(field) {
        let validateText = this[field].replace(/ /g, '');
        let length = validateText.length;

        this[`is${field[0].toUpperCase() + field.slice(1)}Valid`] = length >= this.validationRules[field].min && length <= this.validationRules[field].max;
      },
      selectPeriodName(period) {
        this.period = period.label;
        this.periodImage = period.value;
        this.periodImageUrl = require(`@/assets/img/icon/periods/${period.value}/1x.png`);

        this.periodUploadedFile = null;
        this.validate('period');
      },
      setPeriodName(periodName) {
        this.period = periodName;
        this.periodImageUrl = null;
        this.periodUploadedFile = null;
        this.validate('period');
      },
      dateFormat(classes, date) {
        if (!classes.disabled) {
          classes.disabled = date.getTime() > new Date(this.memorial.deathDate) || date.getTime() < new Date(this.memorial.birthDate);
        }

        return classes;
      },
      rangeToggle(isOpen) {
        this.isRangeValid = true;

        if (isOpen) {
          if ('he' === this.$ml.current) {
            setTimeout(() => {
              const $dateRangePicker = document.querySelector('.daterangepicker');
              if (null !== $dateRangePicker) {
                // console.log($dateRangePicker);
                if ($dateRangePicker.classList.contains('ltr')) {
                  $dateRangePicker.classList.remove('ltr');
                }

                $dateRangePicker.classList.add('rtl');
              }
            }, 10);
          }
        }
      },
      async updateStory(story) {
        this.$emit('updateStory', story);
      },
      async updatePeriod(field = '') {
        this.$store.commit('period/setErrors');
        this.$store.commit('period/setErrorsType2');

        let {...period} = this.periodModel;

        if (field == 'name') {
          period[field] = this.period;
        }
        if (field == 'location') {
          period[field] = this.location;
        }

        period.since = this.range.startDate ? this.$dateUtils.formatDate(this.range.startDate, 'yyyy-MM-dd') : null;
        period.till = this.range.endDate ? this.$dateUtils.formatDate(this.range.endDate, 'yyyy-MM-dd') : null;
        period.thumbnailId = null !== this.periodUploadedFile ? this.periodUploadedFile.id : this.periodModel.thumbnail ? this.periodModel.thumbnail.id : null;
        period.defaultImageName = null !== this.periodImage ? this.periodImage : period.defaultImageName

        this.$emit('updatePeriod', period);
      },
      async uploadImage(event) {
        if (!event.target.files.length) {
          return;
        }

        const file = event.target.files[0];
        const oFReader = new FileReader();
        oFReader.readAsDataURL(file);

        let _this = this;
        oFReader.onloadend = function (oFREvent) {
          _this.periodImageUrl = oFREvent.target.result;
        };

        event.target.value = '';

        let uploadedFile = null;
        if(file) {
          const fileData = {
            file: file,
            type: 'image',
          };
          const response = await this.$store.dispatch('memorial/upload', fileData);
          if(response.status) {
            this.periodUploadedFile = this.$store.getters['memorial/getFile'];

            await this.updatePeriod();
          } else {
            alert(response.data);
            return;
          }

        }
      },
      removeImage() {
        this.periodImageFile = null;
        this.periodImageUrl = null;
      },
    },
    created() {
      this.$store.commit('period/setErrors');
      this.$store.commit('period/setErrorsType2');

      this.locale = this.$ml.current === 'en' ? en : he;
      this.periodItems = this.$ml.get('periods.items');

      this.setPeriodName(this.periodModel.name);

      this.location = this.periodModel.location || '';
      this.range.startDate = this.periodModel.since;
      this.range.endDate = this.periodModel.till;
      this.periodImage = this.periodModel.defaultImageName;

      if(this.periodModel.thumbnail) {
        this.periodImageUrl = this.periodModel.thumbnail.url;
      } else if(this.periodModel.defaultImageName) {
        this.periodImageUrl = require(`@/assets/img/icon/periods/${this.periodModel.defaultImageName}/1x.png`);
      }
    },
  }
</script>

<style lang="scss">
  .z-index-5 {
    z-index: 5;
  }

  .vdp-datepicker {
    position: absolute !important;
  }

  .datepicker {
    input {
      position: absolute !important;
      opacity: 0;
    }
  }

  .daterangepicker .calendars-container {
    flex-wrap: wrap;
  }
</style>